import React, { Component } from 'react';

export default class Hand extends Component{

    selectItem(){
        const { setCurrentItem } = this.props.toolsActions;
        setCurrentItem(0);
        alert('1');
    }

    render(){
        let temp;
        if(this.props.tools.currentItem === 0)
            temp = 'active hand';
        return(
            <div
                className = {temp}
                onClick={this.selectItem.bind(this)}
            >
                Hand
            </div>
        );
    }
}